function Project(props) {
  return (
      
          <div className="col-lg-4 align-self-center">
            <div>
              <h3><a href={props.project.link_web} className="text-reset" target="blank" data-toggle="popover" title="Overview" data-content="A web learning tool for kids to learn Geography in an interactive way. Uses API queries to render dynamic
                                        data and utilizes technologies such as Scalable Vector Graph, Firebase, animate.css, and moment.js. Contributed to all the front-end coding and layout design and some back-end work. Won the in-class
                                        popular vote for Best UI/UX (User Interface/User Experience)." data-placement="bottom">Project: {props.project.name}</a></h3>
            </div>
          </div>
  
  );
}

export default Project;