import Project from './Project.js'
const dataProjects=[
	{
		_id:1,
		name:"social",
		link_web:'https://social-app-vm1993.herokuapp.com/'
	},
	{
		_id:2,
		name:"classroom",
		link_web:'https://classroom-app-vm1993.herokuapp.com/'
	},
	{
		_id:3,
		name:"beauty1993",
		link_web:'https://beauty1993.herokuapp.com/'
	},
	{
		_id:4,
		name:"all project github",
		link_web:'https://github.com/heywirdgirl'
	}
]
function Portfolio() {
  const Projects=dataProjects.map(project=>{
  	return <Project key={project._id} project={project} />
  })
  return (
  	<div className="col-lg-10 offset-lg-1">
        <div className="page-header">
          <h2 id="header2-settings">Một số dự án</h2>
        </div>
          <div className="row align-items-center">
       		{Projects}
       	  </div>
      </div>
  );
}

export default Portfolio;
